*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
.navbar {
    height: 50px;
    background-color: rgba(102, 16, 83, 0.698);
    color: white;
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
  
  .navbar-logo {
    font-size: 20px;
    font-weight: bold;
  }
  
  .list {
    display: flex;
    align-items: center;
    list-style: none;
  }
  
  .list-group {
    margin-right: 20px;
    font-weight: 500;
    cursor: pointer;
  }
  
  .avatar {
    margin-top: 4px;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
  }
  
  .home {
    display: flex;
    padding: 50px 100px;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  
  .card {
    width: 30%;
    padding: 10px;
    border-radius: 20px;
    -webkit-box-shadow: 0px 5px 33px -21px rgba(66, 68, 90, 1);
    -moz-box-shadow: 0px 5px 33px -21px rgba(66, 68, 90, 1);
    box-shadow: 0px 5px 33px -21px rgba(66, 68, 90, 1);
  }
  
  .img {
    width: 100%;
    height: 200px;
    object-fit: cover;
    margin: 20px 0px;
  }
  
  .desc {
    color: #333;
    margin-bottom: 20px;
    line-height: 25px;
  }
  
  .cardButton {
    border: none;
    border-radius: 10px;
    background-color: rgba(53, 16, 102, 0.678);
    padding: 10px;
    color: white;
    font-weight: bold;
    cursor: pointer;
  }
  
  .post {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .post-image {
    width: 100%;
    height: 300px;
    object-fit: cover;
    margin-bottom: 20px;
  }
  
  .post-description {
    padding: 0 100px;
    font-size: 24px;
    font-weight: 300;
    color: gray;
    margin-top: 50px;
  }
  
  .post-description-long {
    padding: 100px;
    padding-top: 50px;
    font-size: 18px;
    line-height: 24px;
  }
  
  .login {
    height: calc(100vh - 50px);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .wrapper {
    width: 60%;
    height: 75%;
    -webkit-box-shadow: 0px 5px 33px -21px rgba(66, 68, 90, 1);
    -moz-box-shadow: 0px 5px 33px -21px rgba(66, 68, 90, 1);
    box-shadow: 0px 5px 33px -21px rgba(66, 68, 90, 1);
    display: flex;
    align-items: center;
    border-radius: 20px;
  }
  
  
  .left,.right{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .center{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }
  
  .or{
    border: 2px solid lightgray;
    border-radius: 50%;
    padding: 10px;
    color: gray;
    background-color: white;
    font-weight: bold;
  }
  
  .line{
    width: 0.5px;
    height: 70%;
    background-color: lightgray;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    z-index: -1;
  }
  
  .login-button{
    width: 150px;
    padding: 15px 25px;
    border-radius: 5px;
    color: white;
    display: flex;
    align-items: center;
    font-weight: bold;
    margin-bottom: 20px;
    cursor: pointer;
  }
  
  .google{
    background-color: #df4930;
  }
  .facebook{
    background-color: #507cc0;
  }
  .github{
    background-color: black;
  }
  
  .icon{
    width: 20px;
    height: 20px;
    margin-right: 10px;
  }
  
  input{
    width: 200px;
    padding: 15px 20px;
    margin-bottom: 20px;
  }
  
  .submit-btn{
    width: 200px;
    background-color: rgba(128, 0, 128, 0.671);
    color: white;
    font-weight: bold;
    border: none;
    padding: 15px 20px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .title-login{
    position: absolute;
    top: 150px;
    color:lightgray
  }
  
  .link{
    color: inherit;
    text-decoration: none;
  }
  
  .sign-up-redirect{
    margin-top: 8px;
    font-size: 17px;
  }

  .link-title{
    color: blue;
  }
  
  .verify{
    display: flex;
    /* text-align: center; */
    justify-content: center;
    /* align-items: baseline; */
    margin-top: 11%;
    height: 350px;
  }
  .verify-wrapper{
    padding-top: 5%;
    width: 20%;
    /* height: 75%; */
    -webkit-box-shadow: 0px 5px 33px -21px rgba(66, 68, 90, 1);
    -moz-box-shadow: 0px 5px 33px -21px rgba(66, 68, 90, 1);
    box-shadow: 0px 5px 33px -21px rgba(66, 68, 90, 1);
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 20px;
  }

  .verify-title{
    margin-bottom: 38px;
    text-align: center;
    font-size: 17px;
  }

  .Signinlogs{
    display: flex;
    flex-direction: row;
  }
  .column{
    flex: 1;
  }
  .column ul{
    list-style-type: none;
  }
  @media screen and (max-width: 992px) {
    .card {
      width: 100%;
      margin-bottom: 50px;
    }
  
    .wrapper {
      width: 65%;
      height: 90%;
      flex-direction: column;
    }
  
    .loginTitle {
      display: none;
    }
  
    .center {
      width: 100%;
      position: relative;
    }
  
    .left {
      padding: 20px;
    }
  
    .right {
      padding: 20px;
    }
  
    .line {
      height: 0.5px;
      width: 300px;
    }
  }





  